.wrapBox {
  width: 80vw;
  border: 1px solid #222222;
  height: 100%;
  margin: 0 auto;
  display: flex;
  border-top: none;
}
.sidebarBox {
  width: 240px;
  background: #333;
  .sidebarTitle {
    box-sizing: border-box;
    background: #fc6423;
    height: 110px;
    font-size: 24px;
    text-align: center;
    padding: 20px 0;
    line-height: 40px;
    color: #ddd;
    p {
      font-size: 12px;
    }
  }
  .sidbarNav {
    .menu {
      margin: 20px 0;
      li {
        height: 36px;
        line-height: 36px;
        padding: 5px 20px;
        font-size: 14px;
        color: #ccc;
        i {
          font-size: 13px!important;
          margin-right: 8px;
        }
        &:hover {
          color: #eee;
          background: #555;
          cursor: pointer;
        }
      }
      .currentItemBagColor{
        background: #555;
        position: relative;
        &:after {
          content: '';
          display: block;
          position: absolute;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background: #BBBBBB;
          right: 30px;
          top: 18px;
        }
      }
    }
  }
  .userInfo {
    padding: 18px 10px;
    height: auto;
    background: #333;
    .userInfoTop {
      width: 100%;
      height: 184px;
      text-align: center;
      font-size: 13px;
      p {
        height: 32px;
        line-height: 32px;
        color: #999;
        b {
          color: #cccccc;
          font-size: 16px;
        }
      }
    }
    .userInfoCenter {
      margin-top: 20px;
      display: flex;
      dl {
        flex: 1;
        height: 44px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &:hover {
          cursor: pointer;
        }
        &:nth-of-type(2) {
          border-left: 1px solid #eee;
          border-right: 1px solid #eee;
        }
      }
    }
  }
}
.contentBox {
  flex: 1;
  background: #333;
  position: relative;
  margin-left: 12px;
  .not-found-image {
    width: 100%;
  }
  .goHomeWrapP {
    text-align: center;
    height: 30px;
    line-height: 30px;
    width: 80px;
    margin: 0 auto;
    border: 1px solid #1890ff;
    background: #1890ff;
    border-radius: 4px;
    &:hover {
      background: #40a9ff;
      cursor: pointer;
    }
    a {
      color: #fff;
      display: block;
    }
  }
  .footer {
    width: 100%;
    position: absolute;
    bottom: 0;
    text-align: center;
  }
}
